import React, { useState } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import './App.css';

import NavigationBar from './components/NavigationBar';
import Container from './components/Container';


function App() {
  let [ loading, setLoading ] = useState(true);
  let [ loaddingClass, setLoadingClass ] = useState("pages-enter pages-enter-active")
  return (
  <div className="App" onLoad={ ()=> {
    setTimeout(()=>{
      setLoadingClass("pages-exit pages-exit-active");
    }, 4700)
    setTimeout(()=> setLoading(false), 5000);
  }}>
   { loading ? (
      <div id="loading" className={ loaddingClass }>
        <img src={ require("./img/loading.svg") } alt="loading svg"/>
      </div>
    ):
    (
      <Router>
        <NavigationBar />
        <Container />
      </Router>
      )
    }
    </div>
  )
}

export default App;
