import React from 'react'

export default function ProgressBar(){
    return(
        <React.Fragment>
            <div class="progress" style={{height: "5px"}}>
                <div class="progress-bar" role="progressbar" aria-valuenow="10" aria-valuemin="0" aria-valuemax="100"></div>
            </div>
        </React.Fragment>
    )
}