import React from 'react'
import VideoIcon from './icons/VideoIcon';

export default function VideoQuality({ value, extension, resolution, requestStream }) {
    return (
        <a className="video-quality" href="#" onClick={ ()=>{
            requestStream(extension, resolution); 
        }}
        >
            <VideoIcon />
            {" "}<span>{ extension } - { resolution }</span>
        </a>
    )
}
